type position = {
  title: string
  subtitle: string
  image: string
  challenge: string
  description: string
  requirements: Array<reqGroup>
  color: string
}

type card = {
  value: position
}

type modalOptions = {
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  component: any
  animation: string
  backgroundColor: string
  props: prop
}

type prop = {
  val: position
}

type reqGroup = {
  title: string
  reqs: Array<string>
}

@Component({
  components: {
    Form,
    SideQuest,
    VueFaqAccordion,
    Cards,
  },
})
export default class Home extends Vue {
  apiRes: ApiResponse = {}

  async mounted() {
    const wow = new WOW({
      boxClass: 'wow',
      animateClass: 'animated',
      offset: 0,
      mobile: true,
      live: false,
    })
    this.$nextTick(() => {
      wow.init()
    })
    await this.setApiRes()
  }

